"use client";
import AdminInput from "@/components/admin/AdminInput";
import TureList from "@/components/admin/TureList";
import Container from "@/components/Container";
import Title from "@/components/ui/Title";
import React, { useEffect } from "react";
import { useQuill } from "react-quilljs";

const page = () => {
  const { quill, quillRef } = useQuill();

  useEffect(() => {
    if (quill) {
      quill.clipboard.dangerouslyPasteHTML("<h1>Skriv indhold her!</h1>");
    }
  }, [quill]);

  return (
    <main>
      <link href="https://cdn.jsdelivr.net/npm/quill@2.0.3/dist/quill.snow.css" rel="stylesheet" />
      <Title className="text-4xl font-bold" text="TURE" />
      <Title text="Her kan du redigere/slette eller lave nye ture" />

      <Container>
        <section className="w-full mt-12">
          <Title className="text-center text-4xl" text="Lav ny tur" />
          <form className="flex flex-wrap px-10 gap-4 mt-4 justify-center">
            <AdminInput className="w-1/4" type="text" name="title" placeholder="Titel" required />
            <AdminInput className="w-1/4" type="text" name="traveltime" placeholder="Transport tid" required />
            <AdminInput className="w-1/4" type="text" name="destination" placeholder="Destination" required />
            <AdminInput className="w-1/4" type="text" name="distance" placeholder="Afstand" required />
            <AdminInput className="w-1/4" type="text" name="price" placeholder="Pris" required />
            <AdminInput className="w-1/4" type="file" name="image1" placeholder="Billede 1" required />
            <AdminInput className="w-1/4" type="file" name="image2" placeholder="Billede 2" required />
            <AdminInput className="w-1/4" type="number" name="rating" max={5} min={0} placeholder="Rating" required />
            <AdminInput className="w-1/4" type="date" name="spacelaunch" placeholder="Rum affyring" required />
            <section className="w-[80%]">
              <div ref={quillRef} />
            </section>
          </form>
        </section>
        <TureList />
      </Container>
    </main>
  );
};

export default page;
